import React, { useState, useEffect } from 'react';
import JobCard from './JobCard';
import { fetchJobs } from '../services/api';

function JobList({ initialFilters = {} }) {
    const [jobs, setJobs] = useState([]);
    const [filters, setFilters] = useState({
        location: '',
        jobType: '',
        salary: '',
        query: '',
        category: ''
    });
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        if (Object.keys(initialFilters).length > 0) {
            console.log("Applying initial filters:", initialFilters);
            setFilters(prev => ({
                ...prev,
                ...initialFilters
            }));
        }
    }, [initialFilters]);

    const searchJobs = async () => {

        console.log("CATEGORY FILTER DEBUG: Current filters:", filters);
        console.log("CATEGORY FILTER DEBUG: Current category filter:", filters.category);

        try {
            setLoading(true);

            // Create a clean copy of filters, removing empty values
            const cleanFilters = {};
            Object.entries(filters).forEach(([key, value]) => {
                if (value && value.trim() !== '') {
                    cleanFilters[key] = value;
                }
            });

            console.log("Searching with filters:", cleanFilters);

            const response = await fetchJobs(cleanFilters);

            const jobsData = response.jobs || response;
            setJobs(Array.isArray(jobsData) ? jobsData : []);
            setError(null);
        } catch (error) {
            console.error('Error fetching jobs:', error);
            setError('Failed to load jobs. Please try again.');
        } finally {
            setLoading(false);
        }
    };

    // Load jobs when component mounts or filters change
    useEffect(() => {
        searchJobs();
    }, [filters]);

    const handleFilterChange = (e) => {
        const { name, value } = e.target;
        setFilters(prev => ({ ...prev, [name]: value }));
    };

    return (
        <div className="container mx-auto p-4">
            <div className="bg-white p-4 rounded-lg shadow-md mb-6">
                <h2 className="text-xl font-semibold mb-4">Search Jobs</h2>

                {filters.category && (
                    <div className="col-span-full bg-blue-100 p-2 rounded mb-4">
                        <p className="text-blue-700">
                            <strong>Category filter:</strong> {filters.category}
                            <button
                                onClick={() => setFilters(prev => ({ ...prev, category: '' }))}
                                className="ml-2 text-red-500 hover:text-red-700"
                            >
                                ✕
                            </button>
                        </p>
                    </div>
                )}

                <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
                    <input
                        type="text"
                        name="query"
                        placeholder="Job title or keyword"
                        className="border p-2 rounded"
                        value={filters.query}
                        onChange={handleFilterChange}
                        onKeyPress={(e) => {
                            if (e.key === 'Enter') {
                                // You could add a specific search function here if needed
                                // Or just let the useEffect handle it based on filters change
                            }
                        }}
                    />
                    <select
                        name="location"
                        className="border p-2 rounded"
                        value={filters.location}
                        onChange={handleFilterChange}
                    >
                        <option value="">All Locations</option>
                        <option value="Remote">Remote</option>
                        <option value="New York">New York</option>
                        <option value="San Francisco">San Francisco</option>
                        <option value="Chicago">Chicago</option>
                        <option value="Seattle">Seattle</option>
                        <option value="Boston">Boston</option>
                        <option value="Austin">Austin</option>
                        <option value="London">London</option>
                        <option value="Toronto">Toronto</option>
                    </select>
                    <select
                        name="jobType"
                        className="border p-2 rounded"
                        value={filters.jobType}
                        onChange={handleFilterChange}
                    >
                        <option value="">All Types</option>
                        <option value="Full-time">Full-time</option>
                        <option value="Part-time">Part-time</option>
                        <option value="Contract">Contract</option>
                        <option value="Internship">Internship</option>
                    </select>
                    <select
                        name="category"
                        className="border p-2 rounded"
                        value={filters.category}
                        onChange={handleFilterChange}
                    >
                        <option value="">All Categories</option>
                        <option value="Technology">Technology</option>
                        <option value="Business">Business</option>
                        <option value="Design">Design</option>
                        <option value="Marketing">Marketing</option>
                        <option value="Healthcare">Healthcare</option>
                        <option value="Education">Education</option>
                        <option value="Hospitality">Hospitality</option>
                        <option value="Finance">Finance</option>
                        <option value="Other">Other</option>
                    </select>
                </div>
            </div>

            {loading ? (
                <div className="flex justify-center items-center h-64">
                    <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
                </div>
            ) : error ? (
                <div className="bg-red-100 text-red-700 p-4 rounded mb-4">
                    {error}
                </div>
            ) : (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                    {jobs.length > 0 ? (
                        jobs.map(job => <JobCard key={job._id} job={job} />)
                    ) : (
                        <div className="col-span-full text-center p-10">
                            <p className="text-gray-500 text-lg">No jobs found matching your criteria</p>
                            <p className="text-gray-500 mt-2">Try adjusting your filters or check back later for new opportunities.</p>
                        </div>
                    )}
                </div>
            )}
        </div>
    );
}

export default JobList;