import React, { useState, useEffect } from 'react';
import { fetchJobs, fetchAllApplications, addJob, updateJobStatus, updateApplicationStatus } from '../services/api';

function AdminDashboard() {
    const [activeTab, setActiveTab] = useState('jobs');
    const [jobs, setJobs] = useState([]);
    const [applications, setApplications] = useState([]);
    const [loading, setLoading] = useState(true);
    const [showAddJobForm, setShowAddJobForm] = useState(false);
    const [newJob, setNewJob] = useState({
        title: '',
        company: '',
        location: '',
        type: 'Full-time',
        salary: '',
        description: ''
    });
    const [error, setError] = useState('');
    const [success, setSuccess] = useState('');

    useEffect(() => {
        const fetchData = async () => {
            try {
                const [jobsData, applicationsData] = await Promise.all([
                    fetchJobs(),
                    fetchAllApplications()
                ]);

                // Check and handle the structure of the response
                console.log('Jobs data:', jobsData);

                // Make sure jobs is an array
                setJobs(Array.isArray(jobsData) ? jobsData :
                    (jobsData.jobs && Array.isArray(jobsData.jobs)) ? jobsData.jobs : []);

                // Make sure applications is an array
                setApplications(Array.isArray(applicationsData) ? applicationsData :
                    (applicationsData.applications && Array.isArray(applicationsData.applications)) ?
                        applicationsData.applications : []);

                setLoading(false);
            } catch (err) {
                console.error('Failed to load data:', err);
                setError('Failed to load data');
                setLoading(false);
            }
        };

        fetchData();
    }, []);


    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setNewJob(prev => ({ ...prev, [name]: value }));
    };

    const handleAddJob = async (e) => {
        e.preventDefault();
        setError('');
        setSuccess('');

        try {
            const addedJob = await addJob(newJob);
            setJobs(prev => [...prev, addedJob]);
            setSuccess('Job added successfully');
            setNewJob({
                title: '',
                company: '',
                location: '',
                type: 'Full-time',
                salary: '',
                description: ''
            });
            setShowAddJobForm(false);
        } catch (err) {
            setError(err.response?.data?.message || 'Failed to add job');
        }
    };

    const handleUpdateJobStatus = async (jobId, status) => {
        try {
            await updateJobStatus(jobId, status);
            setJobs(prev => prev.map(job =>
                job._id === jobId ? { ...job, status } : job
            ));
            setSuccess(`Job status updated to ${status}`);
        } catch (err) {
            setError('Failed to update job status');
        }
    };

    const handleUpdateApplicationStatus = async (applicationId, status) => {
        try {
            await updateApplicationStatus(applicationId, status);
            setApplications(prev => prev.map(app =>
                app._id === applicationId ? { ...app, status } : app
            ));
            setSuccess(`Application status updated to ${status}`);
        } catch (err) {
            setError('Failed to update application status');
        }
    };

    if (loading) {
        return (
            <div className="flex justify-center items-center h-64">
                <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
            </div>
        );
    }

    return (
        <div className="container mx-auto p-4">
            <h1 className="text-2xl font-bold mb-6">Admin Dashboard</h1>

            {error && (
                <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
                    {error}
                </div>
            )}

            {success && (
                <div className="bg-green-100 border border-green-400 text-green-700 px-4 py-3 rounded mb-4">
                    {success}
                </div>
            )}

            <div className="flex border-b mb-4">
                <button
                    className={`py-2 px-4 font-medium ${activeTab === 'jobs' ? 'border-b-2 border-blue-500 text-blue-500' : 'text-gray-500'}`}
                    onClick={() => setActiveTab('jobs')}
                >
                    Manage Jobs
                </button>
                <button
                    className={`py-2 px-4 font-medium ${activeTab === 'applications' ? 'border-b-2 border-blue-500 text-blue-500' : 'text-gray-500'}`}
                    onClick={() => setActiveTab('applications')}
                >
                    Applications
                </button>
            </div>

            {activeTab === 'jobs' && (
                <div>
                    <div className="flex justify-between items-center mb-4">
                        <h2 className="text-xl font-semibold">Job Listings</h2>
                        <button
                            onClick={() => setShowAddJobForm(!showAddJobForm)}
                            className="bg-blue-500 text-white py-1 px-3 rounded hover:bg-blue-600"
                        >
                            {showAddJobForm ? 'Cancel' : 'Add New Job'}
                        </button>
                    </div>

                    {showAddJobForm && (
                        <div className="bg-white p-4 rounded shadow-md mb-4">
                            <h3 className="text-lg font-medium mb-4">Add New Job</h3>
                            <form onSubmit={handleAddJob}>
                                <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
                                    <div>
                                        <label htmlFor="title" className="block text-gray-700 mb-2">Job Title</label>
                                        <input
                                            type="text"
                                            id="title"
                                            name="title"
                                            value={newJob.title}
                                            onChange={handleInputChange}
                                            className="border border-gray-300 p-2 w-full rounded"
                                            required
                                        />
                                    </div>

                                    <div>
                                        <label htmlFor="company" className="block text-gray-700 mb-2">Company</label>
                                        <input
                                            type="text"
                                            id="company"
                                            name="company"
                                            value={newJob.company}
                                            onChange={handleInputChange}
                                            className="border border-gray-300 p-2 w-full rounded"
                                            required
                                        />
                                    </div>

                                    <div>
                                        <label htmlFor="location" className="block text-gray-700 mb-2">Location</label>
                                        <input
                                            type="text"
                                            id="location"
                                            name="location"
                                            value={newJob.location}
                                            onChange={handleInputChange}
                                            className="border border-gray-300 p-2 w-full rounded"
                                            required
                                        />
                                    </div>

                                    <div>
                                        <label htmlFor="type" className="block text-gray-700 mb-2">Job Type</label>
                                        <select
                                            id="type"
                                            name="type"
                                            value={newJob.type}
                                            onChange={handleInputChange}
                                            className="border border-gray-300 p-2 w-full rounded"
                                        >
                                            <option value="Full-time">Full-time</option>
                                            <option value="Part-time">Part-time</option>
                                            <option value="Contract">Contract</option>
                                            <option value="Internship">Internship</option>
                                        </select>
                                    </div>

                                    <div>
                                        <label htmlFor="salary" className="block text-gray-700 mb-2">Salary</label>
                                        <input
                                            type="text"
                                            id="salary"
                                            name="salary"
                                            value={newJob.salary}
                                            onChange={handleInputChange}
                                            className="border border-gray-300 p-2 w-full rounded"
                                            placeholder="e.g., $50,000 - $70,000"
                                            required
                                        />
                                    </div>
                                </div>

                                <div className="mb-4">
                                    <label htmlFor="description" className="block text-gray-700 mb-2">Description</label>
                                    <textarea
                                        id="description"
                                        name="description"
                                        value={newJob.description}
                                        onChange={handleInputChange}
                                        className="border border-gray-300 p-2 w-full rounded"
                                        rows="4"
                                        required
                                    ></textarea>
                                </div>

                                <div className="flex justify-end">
                                    <button
                                        type="submit"
                                        className="bg-blue-500 text-white py-2 px-4 rounded hover:bg-blue-600"
                                    >
                                        Add Job
                                    </button>
                                </div>
                            </form>
                        </div>
                    )}

                    <div className="bg-white rounded shadow overflow-hidden">
                        <table className="min-w-full divide-y divide-gray-200">
                            <thead className="bg-gray-50">
                                <tr>
                                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Title</th>
                                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Company</th>
                                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Location</th>
                                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Type</th>
                                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Status</th>
                                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Actions</th>
                                </tr>
                            </thead>
                            <tbody className="bg-white divide-y divide-gray-200">
                                {jobs.map(job => (
                                    <tr key={job.id}>
                                        <td className="px-6 py-4 whitespace-nowrap">
                                            <div className="font-medium text-gray-900">{job.title}</div>
                                        </td>
                                        <td className="px-6 py-4 whitespace-nowrap text-gray-500">{job.company}</td>
                                        <td className="px-6 py-4 whitespace-nowrap text-gray-500">{job.location}</td>
                                        <td className="px-6 py-4 whitespace-nowrap text-gray-500">{job.type}</td>
                                        <td className="px-6 py-4 whitespace-nowrap">
                                            <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full ${job.status === 'Active' ? 'bg-green-100 text-green-800' :
                                                    job.status === 'Paused' ? 'bg-yellow-100 text-yellow-800' :
                                                        'bg-red-100 text-red-800'
                                                }`}>
                                                {job.status}
                                            </span>
                                        </td>
                                        <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                                            <div className="flex space-x-2">
                                                {job.status !== 'Active' && (
                                                    <button
                                                        onClick={() => handleUpdateJobStatus(job.id, 'Active')}
                                                        className="text-green-600 hover:text-green-900"
                                                    >
                                                        Activate
                                                    </button>
                                                )}
                                                {job.status !== 'Paused' && (
                                                    <button
                                                        onClick={() => handleUpdateJobStatus(job.id, 'Paused')}
                                                        className="text-yellow-600 hover:text-yellow-900"
                                                    >
                                                        Pause
                                                    </button>
                                                )}
                                                {job.status !== 'Closed' && (
                                                    <button
                                                        onClick={() => handleUpdateJobStatus(job.id, 'Closed')}
                                                        className="text-red-600 hover:text-red-900"
                                                    >
                                                        Close
                                                    </button>
                                                )}
                                            </div>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </div>
            )}

            {activeTab === 'applications' && (
                <div>
                    <h2 className="text-xl font-semibold mb-4">Job Applications</h2>

                    <div className="bg-white rounded shadow overflow-hidden">
                        <table className="min-w-full divide-y divide-gray-200">
                            <thead className="bg-gray-50">
                                <tr>
                                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Applicant</th>
                                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Job</th>
                                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Company</th>
                                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Applied Date</th>
                                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Status</th>
                                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Actions</th>
                                </tr>
                            </thead>
                            <tbody className="bg-white divide-y divide-gray-200">
                                {applications.map(app => (
                                    <tr key={app.id}>
                                        <td className="px-6 py-4 whitespace-nowrap">
                                            <div className="font-medium text-gray-900">{app.user.name}</div>
                                            <div className="text-gray-500">{app.user.email}</div>
                                        </td>
                                        <td className="px-6 py-4 whitespace-nowrap text-gray-500">{app.job.title}</td>
                                        <td className="px-6 py-4 whitespace-nowrap text-gray-500">{app.job.company}</td>
                                        <td className="px-6 py-4 whitespace-nowrap text-gray-500">
                                            {new Date(app.appliedDate).toLocaleDateString()}
                                        </td>
                                        <td className="px-6 py-4 whitespace-nowrap">
                                            <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full ${app.status === 'Pending' ? 'bg-yellow-100 text-yellow-800' :
                                                    app.status === 'Reviewing' ? 'bg-blue-100 text-blue-800' :
                                                        app.status === 'Rejected' ? 'bg-red-100 text-red-800' :
                                                            'bg-green-100 text-green-800'
                                                }`}>
                                                {app.status}
                                            </span>
                                        </td>
                                        <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                                            <div className="flex space-x-2">
                                                {app.status === 'Pending' && (
                                                    <button
                                                        onClick={() => handleUpdateApplicationStatus(app.id, 'Reviewing')}
                                                        className="text-blue-600 hover:text-blue-900"
                                                    >
                                                        Review
                                                    </button>
                                                )}
                                                {(app.status === 'Pending' || app.status === 'Reviewing') && (
                                                    <>
                                                        <button
                                                            onClick={() => handleUpdateApplicationStatus(app.id, 'Approved')}
                                                            className="text-green-600 hover:text-green-900"
                                                        >
                                                            Approve
                                                        </button>
                                                        <button
                                                            onClick={() => handleUpdateApplicationStatus(app.id, 'Rejected')}
                                                            className="text-red-600 hover:text-red-900"
                                                        >
                                                            Reject
                                                        </button>
                                                    </>
                                                )}
                                            </div>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </div>
            )}
        </div>
    );
}

export default AdminDashboard;