// services/api.js

import axios from 'axios';

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:8080/api';

// Create axios instance with credentials
const api = axios.create({
    baseURL: API_URL,
    withCredentials: true,
    headers: {
        'Content-Type': 'application/json'
    }
});

// Add token to requests if available
api.interceptors.request.use(
    config => {
        const token = localStorage.getItem('token');
        if (token) {
            config.headers['Authorization'] = `Bearer ${token}`;
        }
        return config;
    },
    error => Promise.reject(error)
);

// Auth Services
export const registerUser = async (userData) => {
    const response = await api.post('/auth/register', userData);
    localStorage.setItem('token', response.data.token);
    return response.data.user;
};

export const loginUser = async (email, password) => {
    const response = await api.post('/auth/login', { email, password });
    localStorage.setItem('token', response.data.token);
    return response.data.user;
};

// src/services/api.js
export const logoutUser = async () => {
    try {
        const response = await api.post('/auth/logout');
        return response.data;
    } catch (error) {
        console.error('Logout service error:', error);
        // Even if the API call fails, we can consider the logout successful on the client side
        return { success: true };
    }
};

export const checkAuthStatus = async () => {
    try {
        const response = await api.get('/auth/me');
        return response.data;
    } catch (error) {
        localStorage.removeItem('token');
        throw error;
    }
};

// Job Services with MongoDB query support
export const fetchJobs = async (filters = {}) => {
    try {
        // Build query string
        const params = new URLSearchParams();
        if (filters.location) params.append('location', filters.location);
        if (filters.jobType) params.append('jobType', filters.jobType); // Make sure this matches backend
        if (filters.salary) params.append('salary', filters.salary);
        if (filters.query) params.append('query', filters.query); // This must match backend
        if (filters.category) params.append('category', filters.category);

        // For debugging - log the actual URL being called
        const url = `/jobs?${params.toString()}`;
        console.log('Calling API with URL:', url);

        const response = await api.get(url);
        console.log('API response:', response.data);
        return response.data;
    } catch (error) {
        console.error('Error in fetchJobs:', error);
        throw error;
    }
};

export const fetchJobById = async (jobId) => {
    const response = await api.get(`/jobs/${jobId}`);
    return response.data;
};

export const addJob = async (jobData) => {
    const response = await api.post('/jobs', jobData);
    return response.data;
};

export const updateJobStatus = async (jobId, status) => {
    const response = await api.patch(`/jobs/${jobId}/status`, { status });
    return response.data;
};

// Application Services
export const applyForJob = async (jobId) => {
    // User ID comes from backend auth middleware
    const response = await api.post('/applications', { jobId });
    return response.data;
};

export const getUserApplications = async (userId) => {
    const response = await api.get(`/applications/user/${userId}`);
    return response.data;
};

export const fetchAllApplications = async () => {
    const response = await api.get('/applications');
    return response.data;
};

export const updateApplicationStatus = async (applicationId, status) => {
    const response = await api.patch(`/applications/${applicationId}/status`, { status });
    return response.data;
};

// User Profile Services
export const getUserProfile = async (userId) => {
    const response = await api.get(`/users/${userId}/profile`);
    return response.data;
};

export const updateUserProfile = async (userId, profileData) => {
    const config = {
        headers: {
            'Content-Type': 'multipart/form-data'
        }
    };
    const response = await api.put(`/users/${userId}/profile`, profileData, config);
    return response.data;
};

// Handle API errors
api.interceptors.response.use(
    response => response,
    error => {
        // Handle token expiration
        if (error.response?.status === 401) {
            localStorage.removeItem('token');
            window.location.href = '/login';
        }
        return Promise.reject(error);
    }
);

export default api;